* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  padding-top: 105px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.load-more.hidden {
  display: none;
}

form {
  width: 100vw;
  z-index: 5;
  background-color: #f1f6f7;
  justify-content: center;
  gap: 20px;
  padding: 30px;
  display: inline-flex;
  position: fixed;
  top: 0;
  box-shadow: 0 3px 8px -3px rgba(136, 157, 168, .8);
}

form input {
  width: 200px;
  border: 1px solid #36434a;
  border-radius: 4px;
  padding: 6px;
}

button {
  color: #d4e0eb;
  background-color: #36434a;
  border: none;
  border-radius: 4px;
  padding: 6px 12px;
  box-shadow: 0 3px 8px -3px rgba(136, 157, 168, .8);
}

button.load-more {
  margin: 20px auto;
  display: block;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.photo-card {
  width: calc(25% - 15px);
  width: 300px;
  background: #f1f6f7 radial-gradient(circle at 50% 0, rgba(241, 246, 247, .65), rgba(241, 246, 247, .35));
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 3px 8px -3px rgba(136, 157, 168, .8);
}

.photo-card > img {
  height: 200px;
  width: 100%;
  object-fit: cover;
  object-position: top;
  display: block;
}

.photo-card .info {
  justify-content: center;
  gap: 5px;
  margin: 0 5px;
  display: flex;
}

.info-item {
  padding: 5px;
  font-size: 12px;
}

